
.page{font-size:5em;color:rgba(0,0,0,0.1)
}
.page{position:absolute;top:0;right:0;bottom:0;left:0
}
#app{transition:all .7s
}
#app,.page{width:100vw;height:100vh
}
.still,.router-animation-left-leave-active,.router-animation-right-leave-active,.fadeIn,.router-animation-fade-enter-active:not(.navigation-page),.fadeOut,.router-animation-fade-leave-active:not(.navigation-page),.slideInLeft,.router-animation-left-enter-active,.slideOutLeft,.slideInRight,.router-animation-right-enter-active,.slideOutRight,.slideInTop,.router-animation-top-enter-active,.slideOutTop,.router-animation-bottom-leave-active,.slideInBottom,.router-animation-bottom-enter-active,.slideOutBottom,.router-animation-top-leave-active,.introShow,.router-animation-top-enter-active.intro-page,.introHide,.router-animation-bottom-leave-active.intro-page,.pageNavigationShow,.page-navigation-animation-enter-active,.pageNavigationHide,.page-navigation-animation-leave-active{animation-fill-mode:forwards;animation-duration:.7s
}
.still,.router-animation-left-leave-active,.router-animation-right-leave-active{animation-name:still
}
.fadeIn,.router-animation-fade-enter-active:not(.navigation-page){animation-name:fadeIn
}
.fadeOut,.router-animation-fade-leave-active:not(.navigation-page){animation-name:fadeOut
}
.slideInLeft,.router-animation-left-enter-active{animation-name:slideInLeft
}
.slideOutLeft{animation-name:slideOutLeft
}
.slideInRight,.router-animation-right-enter-active{animation-name:slideInRight
}
.slideOutRight{animation-name:slideOutRight
}
.slideInTop,.router-animation-top-enter-active{animation-name:slideInTop
}
.slideOutTop,.router-animation-bottom-leave-active{animation-name:slideOutTop
}
.slideInBottom,.router-animation-bottom-enter-active{animation-name:slideInBottom
}
.slideOutBottom,.router-animation-top-leave-active{animation-name:slideOutBottom
}
.introShow,.router-animation-top-enter-active.intro-page{animation-name:introShow
}
.introHide,.router-animation-bottom-leave-active.intro-page{animation-name:introHide
}
.pageNavigationShow,.page-navigation-animation-enter-active{animation-name:pageNavigationShow
}
.pageNavigationHide,.page-navigation-animation-leave-active{animation-name:pageNavigationHide
}
@keyframes still{
0%{
}
100%{
}
}
@keyframes fadeIn{
0%{opacity:0
}
100%{opacity:1
}
}
@keyframes fadeOut{
0%{opacity:1
}
100%{opacity:0
}
}
@keyframes slideInLeft{
0%{transform:translateX(-100%)
}
100%{transform:translateX(0)
}
}
@keyframes slideOutLeft{
0%{transform:translateX(0)
}
100%{transform:translateX(-100%)
}
}
@keyframes slideInRight{
0%{transform:translateX(100%)
}
100%{transform:translateX(0)
}
}
@keyframes slideOutRight{
0%{transform:translateX(0)
}
100%{transform:translateX(100%)
}
}
@keyframes slideInTop{
0%{transform:translateY(-100%)
}
100%{transform:translateY(0)
}
}
@keyframes slideOutTop{
0%{transform:translateY(0)
}
100%{transform:translateY(-100%)
}
}
@keyframes slideInBottom{
0%{transform:translateY(100%)
}
100%{transform:translateY(0)
}
}
@keyframes slideOutBottom{
0%{transform:translateY(0)
}
100%{transform:translateY(100%)
}
}
@keyframes introShow{
from{transform:translateY(-200px);opacity:0.25
}
to{transform:translateY(0);opacity:1
}
}
@keyframes introHide{
to{transform:translateY(-200px);opacity:0.25
}
}
@keyframes pageNavigationShow{
0%{transform:translateX(-50%) translateY(200%);opacity:0
}
100%{transform:translateX(-50%) translateY(0);opacity:1
}
}
@keyframes pageNavigationHide{
0%{transform:translateX(-50%) translateY(0);opacity:1
}
100%{transform:translateX(-50%) translateY(200%);opacity:0
}
}
@keyframes page-move{
0%{transform:scale(1, 1)
}
20%{transform:scale(1, 0.5)
}
40%{transform:scale(1, 0.5)
}
100%{transform:scale(1, 1)
}
}
@keyframes li-move-right{
0%{transform:translateX(0px)
}
20%{transform:translateX(5px)
}
40%{transform:translateX(-2px)
}
60%{transform:translateX(1px)
}
80%{transform:translateX(0px)
}
100%{transform:translateX(0px)
}
}
@keyframes li-move-left{
0%{transform:translateX(0px)
}
20%{transform:translateX(-5px)
}
40%{transform:translateX(2px)
}
60%{transform:translateX(-1px)
}
80%{transform:translateX(0px)
}
100%{transform:translateX(0px)
}
}
*{box-sizing:border-box
}
body,html{height:100vh
}
body{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#333;background:#333
}
#app{position:relative;perspective:1200px;transform-style:preserve-3d;overflow:hidden
}
.page{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-wrap:nowrap;flex-wrap:nowrap;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;-ms-flex-line-pack:stretch;align-content:stretch
}
.page:after{content:"";display:block;position:fixed;right:0;bottom:0;left:0;height:150px
}
.intro-page{z-index:1
}
.navigation-page{z-index:2
}
.page{z-index:3
}
.pages-navigation{z-index:4
}



